/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import linkedinIcon from '../../content/assets/linkedin-icon.png';
import instagramIcon from '../../content/assets/instagram-icon.png';
import { Pane, Link, Text } from 'evergreen-ui';

const Bio = ({ mode = 'author' }) => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "/bio-pic.png/" }) {
        childImageSharp {
          fixed(width: 80, height: 80) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
            summary
          }
          social {
            instagram
            linkedin
          }
          siteUrl
        }
      }
    }
  `);

  const { author, social, siteUrl } = data.site.siteMetadata;
  return (
    <Pane display="flex" flexDirection="column">
      {mode === 'author' && (
        <Text>
          Written by{' '}
          <Link className="logo-link" href="/about">
            {author.name}
          </Link>
        </Text>
      )}
      <br></br>
      {` `}
      <Pane display="flex" alignItems="center">
        <img
          src={instagramIcon}
          alt="insta"
          style={{ width: '30px', marginBottom: '0px' }}
        />
        <Link
          className="logo-link"
          size="600"
          href={`https://instagram.com/${social.instagram}`}
          target="_blank"
        >
          Instagram
        </Link>
      </Pane>

      <Pane display="flex" alignItems="center">
        <img
          src={linkedinIcon}
          alt="linkedin"
          style={{ width: '30px', marginBottom: '0px' }}
        />
        <Link
          className="logo-link"
          size="600"
          href={social.linkedin}
          target="_blank"
        >
          Linkedin
        </Link>
      </Pane>
    </Pane>
  );
};

export default Bio;
